.grid-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #5abe9d;
    border: 1px solid black;
    width: 100%;


}

.grid-item-rooms {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
}

.grid1 {
    display: flex;
    padding-top: '1px';
    align-items: flex-end;
    justify-content: center;
    background-color: #f5f5f5;
    border: 1px solid #e0e0e0;
    white-space: nowrap;

}

.draggable {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #5abe9d;
    border: 1px solid #e0e0e0;
    border-radius: 30px;
    white-space: nowrap;
    color: white;
    height: 100%;
}

.draggable-in {
    height: 90%;
    box-sizing: border-box;
    font-size: 0.5rem;
}

.supermonth {
    display: flex;
    padding-top: 1px;
    position: relative;
    align-items: flex-end;
    justify-content: center;
    height: 25px;
    background-color: gainsboro;
    border: 1px solid beige;
}

.lastday {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 12px;
    box-sizing: border-box;
    background-color: red;

}

.monthdays {
    font-size: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px gray;
}